import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'

export const CLEAR_CART = 'CLEAR_CART'
export const ERROR = 'error'
export const CLEAR_ERROR = 'clear_error'
const url = `${process.env.REACT_APP_BACKEND_API}/paystack/pay`


export function useCheckout() {
    const token = window.localStorage.getItem('token')
    const navigate = useNavigate()

    const checkoutCart = (email, firstName, lastName, amount) => {
             return async (dispatch) => {
                try{
                    //get values and make API call
                    const values = { email, firstName, lastName, amount }
                    const result = await axios.post(url, values, {
                        headers: {
                            'content-type': 'application/json', 'Authorization': `Bearer ${token}`
                        }
                    })
        
                    dispatch({
                        type: CLEAR_CART,
                        payload: result.data.reference
                    })
                    navigate('/cart')
                    Swal.fire(
                        'success',
                        result.data.message,
                        'success')
                } catch(err){
                    dispatch({
                        type:  ERROR,
                        payload: err.response.data.message
                    })
                    navigate('/signIn')
                    setTimeout(() => {
                        dispatch({
                            type:  CLEAR_ERROR,
                        })
                    },2000)
                }
         
        }
    }

    //what hook returns
    return (
        checkoutCart
    )
}


